/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

body {
  background-color: hsl(30, 38%, 92%);
  font-family: "Montserrat", sans-serif;
}

p,
h1,
span {
  font-size: 1.4rem;
  margin: 0;
}

.card {
  width: 34.3;
  height: 63.7rem;
  margin-top: 2.7rem;
  margin-bottom: 2.7rem;
  background-color: white;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .card {
    width: 60rem;
    height: 45rem;
    margin-top: 17.5rem;
    margin-bottom: 17.5rem;
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
  }
}
.card .img {
  width: 100%;
  height: 23.9rem;
  background-image: url("../img/image-product-mobile.jpg");
  background-size: cover;
  background-position: center center;
  border-top-right-radius: 2rem;
  border-top-left-radius: 2rem;
}
@media (min-width: 768px) {
  .card .img {
    background-image: url("../img/image-product-desktop.jpg");
    width: 30rem;
    height: 45rem;
    border-top-left-radius: 2rem;
    border-top-right-radius: 0rem;
    border-bottom-left-radius: 2rem;
  }
}

.informacion-contenido {
  margin: 1.9rem 3.5rem;
}
.informacion-contenido .title {
  text-transform: uppercase;
  letter-spacing: 1rem;
  color: hsl(228, 12%, 48%);
}
.informacion-contenido h1 {
  font-family: "Fraunces", serif;
  font-size: 3rem;
  text-align: start;
  color: hsl(212, 21%, 14%);
  font-weight: 700;
  line-height: normal;
}
@media (min-width: 768px) {
  .informacion-contenido h1 {
    margin-top: 1.5rem;
    margin-bottom: 3rem;
  }
}
.informacion-contenido .information {
  font-size: 1.7rem;
  margin-top: 1.7rem;
  color: hsl(228, 12%, 48%);
}
@media (min-width: 768px) {
  .informacion-contenido .information {
    font-size: 1.4rem;
  }
}

button {
  width: 23rem;
  height: 4.9rem;
  background-color: hsl(158, 36%, 37%);
  color: white;
  font-weight: 700;
  border: none;
  border-radius: 1rem;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  button {
    margin-bottom: 3rem;
  }
}
button:hover {
  cursor: pointer;
  background-color: #244c3d;
}

.precio {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  display: flex;
  color: hsl(228, 12%, 48%);
  align-items: center;
  gap: 2rem;
}
.precio .primary {
  font-size: 2.7rem;
  color: hsl(158, 36%, 37%);
  font-family: "Fraunces", serif;
}
.precio .secondary {
  text-decoration: line-through;
}